body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Game {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 37vw;
    margin: 0 auto;
    position: relative;
}

.Game .Game__cell {
    background: #ececec;
    flex: 0 0 25%;
    align-items: stretch;
    text-align: center;
    font-size: 32px;
    line-height: 9vw;
}

.Game .Game__cell.empty {
    background: #fff;
}

.Game .Game__cell:hover {
    background: #e0e0e0;
    cursor: pointer;
}

.Game .Game__cell.empty:hover {
    background: #fff;
    cursor: default;
}

@media screen and (max-width: 992px) {
    .Game {
        max-width: 60vw
    }
    .Game .Game__cell {
        font-size: 26px;
        line-height: 16vw;
    }
}


@media screen and (max-width: 768px) {
    .Game {
        max-width: 80vw
    }
    .Game .Game__cell {
        line-height: 20vw;
    }
}
.Info {
    text-align: center;
}

.Info .Info__counter {
    font-size: 18px;
    font-weight: 700;
    margin: 0 10px 0 4px;
}

.Info .Info__button {
    margin: 0 4px;
}

.Info .Info__greetings {
    margin: 0 10px;
    font-size: 18px;
    font-weight: 700;
}
.App__heading {
    text-align: center;
}

.App__lead {
    color: #777;
    font-size: 14px;
    text-align: center;
}
